import React,{ Component } from 'react'
import './footer.css'
import guide from '../icons/guide.svg'
import pay from '../icons/pay.svg'
import way from '../icons/way.svg'
import service from '../icons/service.svg'
import tel from '../icons/tel.svg'
import about from '../icons/about.svg'
import free from '../icons/free.svg'
import link from '../icons/link.svg'



import taobao from '../icons/taobao.svg'
import wx from '../icons/wx.svg'
import telphone from '../icons/telphone.svg'
class Footer extends Component{
  render(){
    return (
      <div className='footer'>
        <div className="footerup">
          <div>
            <div className="helpmessege">
              <h3></h3>
              <div>
                <ul>
                  <li><img src={guide} alt=""/><a href="#"></a></li>
                  <li><img src={pay} alt=""/><a href="#"></a></li>
                  <li><img src={way} alt=""/><a href="#"></a></li>
                  <li><img src={service} alt=""/><a href="#"></a></li>
                </ul>
                <ul>
                  <li><img src={tel} alt=""/><a href="#"></a></li>
                  <li><img src={about} alt=""/><a href="#"></a></li>
                  <li><img src={free} alt=""/><a href="#"></a></li>
                  <li><img src={link} alt=""/><a href="#"></a></li>
                </ul>
              </div>
            </div>
            <div className="footertcontain">
              <h3></h3>
              <div>
                <span></span>
              </div>
              <div>
                <img src={taobao} alt=""/>
                <img src={wx} alt=""/>
              </div>
            </div>
            <div className='telphone'>
              <h3></h3>
              <div>
                <img src={telphone} alt=""/>
                <div>
                  <span>400 113 5353</span>
                  <span>[ 9:00-18:00]</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="footerdown">
          <div>
            <div>
              <a href="#"></a>
            </div>
            <div>
              <span> Copyright 2015-2025 Morethan.cc All rights reserved.</span>
              <span>9344505</span>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
export default Footer



